import { Col, Divider, List, Row } from "antd";
import pinyin from "../config/pinyin";
import "../App.css";
import { LeftOutlined } from "@ant-design/icons";
import { Link } from "react-router-dom";
import { useMemo } from "react";
import _ from "lodash";

const HZList = () => {
  const sortedPinyin = useMemo(() => {
    return _.sortBy(pinyin, (p) => p.word);
  }, []);

  return (
    <div>
      <div className="header">
        <Row>
          <Col span={10}>
            <Link to="/">
              <LeftOutlined style={{ fontSize: "18px", color: "white" }} />
            </Link>
          </Col>
          <Col span={10}>
            <span style={{ fontSize: "18px" }}>常见汉字</span>
          </Col>
        </Row>
      </div>
      <div className="list">
        <List
          bordered
          split
          itemLayout="horizontal"
          dataSource={sortedPinyin}
          renderItem={(item, index) => (
            <>
              <Link to={`/?word=${item.word}`}>
                <List.Item>
                  <List.Item.Meta title={item.pinyin} description={item.word} />
                </List.Item>
              </Link>
              <Divider
                orientationMargin={1}
                style={{
                  lineHeight: 1,
                  marginTop: 1,
                  marginBottom: 1,
                }}
              />
            </>
          )}
        />
      </div>
    </div>
  );
};

export default HZList;
