import React, { useCallback, useEffect, useState } from "react";
import "./App.css";
import { Button, Col, Input, Row, Space } from "antd";
import HanziWriter from "hanzi-writer";
import Grid from "./components/Grid";
import pinyin from "./config/pinyin";
import AudioPlayer from "./components/AudioPlayer";
import { Link, useLocation } from "react-router-dom";
import _ from "lodash";

const svgId = "grid-background-target";

function App() {
  const [hzWriter, setHzWriter] = useState<HanziWriter>();
  const [hzPinyin, setHzPinyin] = useState<string>();
  const [hzVoiceSrc, setHzVoiceSrc] = useState<string>();
  const [word, setWord] = useState("");
  const location = useLocation();

  const resetGrid = useCallback(() => {
    const ele = document.getElementById(svgId);
    const gridLines = ele?.getElementsByTagName("line");
    const children = new Array<SVGLineElement>();
    _.forEach(gridLines, (l) => children.push(l));
    while (ele?.firstChild) {
      ele?.removeChild(ele?.firstChild);
    }
    // 将要保留的元素重新添加到父元素中
    _.forEach(children, (l) => ele?.appendChild(l));
  }, []);

  useEffect(() => {
    if (word) {
      resetGrid();
      var writer = HanziWriter.create(svgId, word, {
        width: 200,
        height: 200,
        padding: 5,
        showOutline: true,
        delayBetweenLoops: 1500,
      });
      writer.hideCharacter();
      setHzWriter(writer);
      setHzVoiceSrc(`.\\mp3\\${word}.mp3`);
    } else {
      setHzVoiceSrc("");
    }
    setHzPinyin(pinyin.find((p) => p.word === word)?.pinyin || "");
  }, [resetGrid, word]);

  useEffect(() => {
    if (location.search) {
      const searchParams = new URLSearchParams(location.search);
      const hz = searchParams.get("word");
      setWord(hz || "");
    }
  }, [location.search]);

  const handleClick = useCallback(() => {
    hzWriter?.showCharacter();
    hzWriter?.loopCharacterAnimation();
  }, [hzWriter]);

  const handleInputChange = useCallback(
    (text: string) => {
      if (!text) {
        hzWriter?.hideCharacter();
      }
      setWord(text);
    },
    [hzWriter]
  );

  return (
    <div>
      <Space
        className="container"
        direction="vertical"
        size={"middle"}
        align={"center"}
      >
        <Row gutter={4} align={"middle"}>
          <Col span={16}>
            <Input
              maxLength={1}
              size={"large"}
              placeholder="输入单个汉字"
              onChange={(e) => handleInputChange(e.target.value)}
              value={word}
            ></Input>
          </Col>
          <Col span={8}>
            <Button
              size={"large"}
              type="primary"
              onClick={() => handleClick()}
              disabled={word === ""}
            >
              显示笔画
            </Button>
          </Col>
        </Row>
        <Grid />
        <Space direction={"horizontal"} size={"large"} align={"start"}>
          <div className="pinyin">
            <span>拼音：{hzPinyin}</span>
          </div>
          {hzVoiceSrc && <AudioPlayer src={hzVoiceSrc || ""} />}
        </Space>
        <Space>
          <Link to="/hzlist">常见汉字</Link>
        </Space>
      </Space>
    </div>
  );
}

export default App;
